:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.is-active {
  pointer-events: none;
  background-color: #ff6b01;
}

img {
  max-width: 100%;
  height: 100%;
  width: 100%;
  display: block;
}

.film-card {
  flex-direction: column;
  flex-basis: 30%;
  display: flex;
}

